<template>
  <div class="unit-input">
    <input type="number" class="input unit-input__input" v-model="value.value">
    <select-comp v-model="select" class="unit-input__select" :key="re"></select-comp>
  </div>
</template>

<script>
import SelectComp from "@/components/SelectComp";

export default {
  name: "ValueSelect",
  props: {
    value: {
      type: Object,
      default: ()=>{ return { type: 'time', value: 0 } }
    }
  },
  data: ()=>({
    re: 0,
    select: {
      options: [
        { name: 'minutes', value: 'm', selected: true },
        { name: 'hours', value: 'h' },
      ]
    }
  }),
  components: { SelectComp },
  mounted() {
    if (this.value.type === 'radius') {
      this.re++
      this.select.options = [
        { name: 'm', value: 'mmm', selected: true },
        { name: 'km', value: 'km' },
      ]
    }
  }
}
</script>

